import { useGlobalContext } from "../pages/LoggedInContext";
import {  useEffect } from "react";
import { useNavigate } from "react-router-dom";
function Mycart() {
    const navigate = useNavigate();
    const { cart,setcart,price,setprice,counts,setCounts,isLoggedIn} = useGlobalContext();
    useEffect(() => {
        const newCounts = {};
        var dupprice = 0;
        cart.forEach(item => {
            newCounts[item.id] = item.count || 1;
            dupprice += parseInt(item.price) * parseInt(item.count || 1)
        });
        setprice(dupprice);
        setCounts(newCounts);
    }, [cart]);
    function handleMinus(id) {
        const newCounts = { ...counts };
        if (newCounts[id] > 1) {
            newCounts[id] = newCounts[id] - 1;
            setCounts(newCounts);
            const newCart = cart.map((item) =>
                item.id === id ? { ...item, count: newCounts[id] } : item
            );
            setcart(newCart);
        } else {
            handleRemove(id);
        }
    }
    function handlePlus(id) {
        const newCounts = { ...counts };
        newCounts[id] = (newCounts[id] || 0) + 1;
        setCounts(newCounts);
        const newCart = cart.map((item) =>
            item.id === id ? { ...item, count: newCounts[id] } : item
        );
        setcart(newCart);
    }
    console.log(price)
    console.log(cart)
    console.log(counts)
    function handleRemove(id) {
        const newCart = cart.filter((item) => item.id !== id);
        setcart(newCart);
        const newCounts = { ...counts };
        delete newCounts[id];
        setCounts(newCounts);
    }
    function handleOrder(){
        if(isLoggedIn){
            navigate('/orderconfirmation')
        }
        else{
            navigate('/login')
        }
    }
    return (
        <>
            <div className="mycart">
                <h3 className="headermycart">MY CART</h3>
                {cart.map((el) => (
                    <div className="cartbox">
                        <div className="cartimgstyle">
                            <img src={el.photo} alt="ok"></img>
                        </div>
                        <div className="fontstyle">
                            <h2>{el.name}</h2>
                            <h2>&#x20b9; {el.price}</h2>
                        </div>
                        <div className="countbox">
                            <button onClick={() => handleMinus(el.id)}>-</button>
                            <h3>{counts[el.id] || 1}</h3>
                            <button onClick={() => handlePlus(el.id)}>+</button>
                        </div>
                    </div>
                ))}
            </div>
            <div className="cartbottom">
                <div className="totalprice">
                    <h3>Total Ammount</h3>
                    <h3>&#x20b9; {price || 0}</h3>
                </div>
                <div className="orderbtn">
                    <button className="addtocart" onClick={() => handleOrder()}>Place Order</button>
                </div>
            </div>
        </>
    )
}
export default Mycart;