import './App.css';
import { BrowserRouter, Routes, Route, Outlet } from 'react-router-dom';
import Couches from './pages/couches';
import Chairs from './pages/chairs';
import Login from './pages/login';
import Premium from './pages/premium';
import Orderconfirm from './pages/orderconfirm';
import React from 'react';
import { AppContex } from './pages/LoggedInContext';
function App() {
  return (
    <>
      <AppContex>
        <BrowserRouter>
          <Routes>
            <Route path='/' index element={<Couches />}></Route>
            <Route exact path='/categories/chairs' element={<Chairs />}></Route>
            <Route exact path='/categories/premium' element={<Premium/>}></Route>
            <Route exact path='/orderconfirmation' element={<Orderconfirm/>}></Route>
            <Route exact path='/login' element={<Login />}></Route>
          </Routes>
        </BrowserRouter>
        <div>
          <Outlet />
        </div>
      </AppContex>
    </>
  );
}

export default App;
