import React, { useEffect, useState } from "react";
import { useNavigate, useLocation } from "react-router-dom";
import { useGlobalContext } from "../pages/LoggedInContext";
function Header() {
  const { isLoggedIn, username, setLoggedIn } = useGlobalContext();
  const [activeButton, setActiveButton] = useState(1);
  console.log(isLoggedIn)
  const location = useLocation();
  const navigate = useNavigate();
  useEffect(() => {
    switch (location.pathname) {
      case "/":
        setActiveButton(1);
        break;
      case "/categories/chairs":
        setActiveButton(2);
        break;
      case "/categories/premium":
        setActiveButton(3);
        break;
      default:
        setActiveButton(0);
    }
  }, [location.pathname]);
  function handleCouches(buttonNumber) {
    if (activeButton !== buttonNumber) {
      setActiveButton(buttonNumber);
    }
    navigate("/")
  }
  function handleChairs(buttonNumber) {
    if (activeButton !== buttonNumber) {
      setActiveButton(buttonNumber);
    }
    navigate("/categories/chairs")
  }
  function handlePremium(buttonNumber) {
    if (activeButton !== buttonNumber) {
      setActiveButton(buttonNumber);
    }
    if (isLoggedIn) {
      navigate("/categories/premium")
    }
    else {
      navigate("/login")
    }
  }
  function handleLogin() {
    console.log(isLoggedIn)
    if (!isLoggedIn) {
      navigate("/login")
    }
  }
  function handleLogout() {
    setLoggedIn(false)
    navigate("/")
  }
  return (
    <>
      <div className="topfixedpart">
        <div className="headerpart">
          <h3>SITBACK</h3>
          <div className="productsroutes">
            <button className={`headerbtn ${activeButton === 1 ? 'active' : ''}`} onClick={() => handleCouches(1)} >couches</button>
            <button className={`headerbtn ${activeButton === 2 ? 'active' : ''}`} onClick={() => handleChairs(2)} >chairs</button>
            <button className={`headerbtn ${activeButton === 3 ? 'active' : ''}`} onClick={() => handlePremium(3)} >premium</button>
          </div>
          {
            isLoggedIn ? (
              <div class="right-nav">
                <button className="loginbtn"><h4>{`${username}`}</h4></button>
                <div class="menu">
                    <button className="addtocart" onClick={() => handleLogout()}>Logout</button>
                </div>
              </div>

            ) :
              (
                <button className="loginbtn" onClick={() => handleLogin()}>Login Now</button>
              )
          }
        </div>
      </div>
    </>
  )
}
export default Header;