import { useGlobalContext } from "../pages/LoggedInContext";
import Mycart from "./Mycart";
import {BsShieldFillCheck} from "react-icons/bs";
function Products(props) {
    const {cart,setcart} = useGlobalContext();
    const data = props.productdata
    function handleCart(index){
        const alreadyInCart = cart.some(item => item.id === data[index].id);
        if (!alreadyInCart) {
            setcart([...cart, data[index]]);
        }
        else{
            alert("product already in cart")
        }
    }
    console.log(cart)
    return (
        <>
            <div className="mainproducts">
                <div className={`productflex ${cart.length != 0 ? 'reducewidth' : ''}`}>
                    {data.map((el,index) => (
                        <div className="products">
                            <img src={el.photo} alt="ok"></img>
                            <div className="namepriceflex">
                                <h3>{el.name}</h3>
                                <h3>&#x20b9; {el.price}</h3>
                            </div>
                            <p className="dummytext">Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s,</p>
                            <h3 style={{color:"green"}}><BsShieldFillCheck className="shield"/> {`${el.guarantee} yaers gaurantee`}</h3>
                            <div className="line"></div>
                            <div className="flexend">
                                  <button onClick={() => handleCart(index)} className="addtocart">
                                      ADD TO CART
                                  </button>
                            </div>
                        </div>
                    ))}
                </div>
                <div className="fixedcart">
                   {
                       cart.length != 0  ? (
                           <Mycart />
                        ) : (
                            null
                        )
                    }
                </div>
            </div>
        </>
    )
}
export default Products;