// LoggedInContext.js
import { createContext, useContext, useState } from 'react';
const LoggedInContex = createContext();
export const useGlobalContext = () => useContext(LoggedInContex)
export const AppContex = ({ children }) => {
    const [isLoggedIn, setLoggedIn] = useState(false);
    const [username, setUsername] = useState("");
    const [cart,setcart] = useState([]);
    const [price,setprice] = useState(0);
    const [counts, setCounts] = useState({});
  
    return (
      <LoggedInContex.Provider value={{ isLoggedIn, username, setLoggedIn, setUsername,cart, setcart,price,setprice,counts,setCounts}}>
        {children}
      </LoggedInContex.Provider>
    );
};
